import Media from "./../index";
import Uploader from '@/components/Uploader';
export default {
  components: {
    Media: Media,
    Uploader: Uploader
  },
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    selectable: {
      type: Boolean,
      default: false
    },
    isBannerMode: {
      type: Boolean,
      default: false
    }
  },
  data: function data() {
    return {
      mediaItemLayout: {
        xs: 8,
        sm: 8,
        md: 8,
        lg: 10,
        xl: 4,
        gutter: 1,
        justify: 'start'
      }
    };
  },
  watch: {
    isBannerMode: {
      handler: function handler(val) {
        this.isBannerMode = val;
      }
    }
  },
  methods: {
    handleSuccessUpload: function handleSuccessUpload() {
      this.$refs.media.init();
    },
    handleMediaSelect: function handleMediaSelect(media) {
      this.$emit('select', media);
    },
    handleRefreshClick: function handleRefreshClick() {
      this.$refs.media.init();
    }
  }
};